

.page-wrap, #root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: #f5f5f5; }

.main {
    margin: 0 auto;
    padding: 20px;
    margin: 20px;
    border: 1px solid #dedede;
    border-radius: 10px;
    background-color: #fff;
    .sidebar {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center; }
    .tasks-wrap {
        padding: 10px;
        border: 1px solid #dedede;
        border-radius: 5px;
        .task {
            margin: 10px 0px;
            box-shadow: 0px 0px 20px 0px rgba(#000, .25);
            transition: transform .25s ease, box-shadow .10s ease;
            &:hover {
                transform: translateY(-5px);
                box-shadow: 5px 5px 20px 0px rgba(#000, .25); } }
        .sort-menu {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;
            width: 100%; } } }

.login-form {
    width: 300px;
    margin: 0 auto; }

.task-header {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    >div {
        margin-right: auto; }
    button {
        margin: 0 10px;
        &:last-of-type {
            margin-right: 0px; } } }

.pagination-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 20px; }
